import { Component } from 'preact'
import * as classnames from 'classnames'

const SmallMetric = ({ name, value, warn }) => (
  <div
    class={classnames('IteratorResult__metric IteratorResult__metric--small', {
      'IteratorResult__metric--warn': warn
    })}
  >
    {name}: {value}
  </div>
)

const Radiuses = ({ radiuses }) => (
  <table style="width: 100%; font-size: 12px">
    <thead style="font-size: 10px">
      <td>Radius</td>
      <td style="width: 14%">Pages</td>
      <td style="width: 14%">Count</td>
      <td style="width: 14%">Trash</td>
      <td style="width: 14%">Warn</td>
      <td style="width: 14%">Seen</td>
      <td style="width: 14%">Est.</td>
    </thead>
    <tbody>
      {radiuses.map(radius => (
        <tr>
          <td>{radius.radius.max}</td>
          <td>{radius.totals.pages || '-'}</td>
          <td>{radius.totals.count || '-'}</td>
          <td style={radius.totals.trash ? 'color: #c33' : ''}>
            {radius.totals.trash || '-'}
          </td>
          <td style={radius.totals.warn ? 'color: #c33' : ''}>
            {radius.totals.warn || '-'}
          </td>
          <td>{radius.totals.seen || '-'}</td>
          <td>{radius.totals.estimated || '-'}</td>
        </tr>
      ))}
    </tbody>
  </table>
)

export default class IteratorResult extends Component {
  render({ iterator, showExtra, showGood }) {
    const fewResults = iterator.totals.count === 0
    const tooManySteps = iterator.totals.count / iterator.totals.pages < 5
    if (!showGood && !(fewResults || tooManySteps || iterator.error)) {
      return null
    }
    return (
      <div
        class={classnames('IteratorResult', {
          'IteratorResult--error': iterator.error
        })}
        onClick={this.props.onClick}
      >
        <div class="IteratorResult__name">{iterator.name}</div>
        <div>
          <span
            class={classnames(
              'IteratorResult__metric IteratorResult__metric--big',
              {
                'IteratorResult__metric--warn': fewResults
              }
            )}
            title="total items"
          >
            {iterator.totals.count}
          </span>
          <span
            class="IteratorResult__metric IteratorResult__metric--big"
            title="steps"
          >
            {' / '}
          </span>
          <span
            class={classnames(
              'IteratorResult__metric IteratorResult__metric--big',
              {
                'IteratorResult__metric--warn': tooManySteps
              }
            )}
            title="steps"
          >
            {iterator.totals.pages}
          </span>
        </div>
        {iterator.error && (
          <div class="IteratorResult__metric IteratorResult__metric--middle">
            {iterator.error.match(/[^:]*/)[0]}
          </div>
        )}
        {showExtra &&
        iterator.radiuses.length > 0 && (
          <Radiuses radiuses={iterator.radiuses} />
        )}
      </div>
    )
  }
}



