const calculateSummary = report => {
  const map = {}

  for (let fixture of report) {
    for (let iterator of fixture.iterators) {
      const sourceName = iterator.name.split(' ')[0]
      map[sourceName] = map[sourceName] || { sourceName, totals: { pages: 0, count: 0, trash: 0, errors: 0 } }
      const source = map[sourceName]
      // add totals
      source.totals.pages += iterator.totals.pages
      source.totals.count += iterator.totals.count
      source.totals.trash += iterator.totals.trash
      source.totals.errors += iterator.error ? 1 : 0
      // Look for items
      for (let radius of iterator.radiuses) {
        if (!source.sampleItemUrl) {
          const val = radius.steps[0].values[0]
          if (val) {
            source.sampleItemUrl = val.url
          }
        }
      }
    }
  }

  return [...Object.entries(map)].sort().map(x => x[1])
}

export default ({ report }) => {
  const summary = calculateSummary(report)
  return (
    <table class="Summary">
      <tr>
        <th class="Summary__sourceName">Source name</th>
        <th class="Summary__count">Pages</th>
        <th class="Summary__count">Count</th>
        <th class="Summary__count">Trash</th>
        <th class="Summary__count">Errors</th>
        <th class="Summary__itemUrl">Item URL Sample</th>
      </tr>
      {summary.map(line => (
        <tr class="Summary__line">
          <td class="Summary__sourceName">{line.sourceName}</td>
          <td class="Summary__count">{line.totals.pages}</td>
          <td class="Summary__count">{line.totals.count}</td>
          <td class="Summary__count">{line.totals.trash}</td>
          <td class="Summary__count">{line.totals.errors}</td>
          <td class="Summary__itemUrl">
            {line.sampleItemUrl && (
              <a href={line.sampleItemUrl}>{line.sampleItemUrl}</a>
            )}
          </td>
        </tr>
      ))}
    </table>
  )
}



