import { Component } from 'preact'
import * as R from 'ramda'
import { inspect } from 'util'
import Inspector from 'react-inspector'

const itemsToTable = item => {
  const { attributes, geolocation, ...rest } = item
  return [item]
}

function formatWrongValues(values) {
  const grouped = R.countBy(inspect)(values)
  return R.toPairs(grouped)
    .map(([a, b]) => `${a}${b > 1 ? ` (${b} times)` : ''}`)
}

const getGroupedFails = (kind, iterator) => {
  const fails = R.flatten(R.flatten(iterator.radiuses.map(x => x.steps)).map(x => x[kind]))
  const groupedFails = fails.reduce((acc, discard) => {
    discard.fails.forEach(fail => {
      acc[fail.reason] = acc[fail.reason] || []
      acc[fail.reason].push(fail.value)
    })
    return acc
  }, {})
  return R.toPairs(groupedFails).map(([reason, values]) => ({
    reason,
    values: formatWrongValues(values)
  }))
}

export default class StepsDetails extends Component {
  render(props) {
    const { iterator } = props
    const groupedDiscards = getGroupedFails('discards', iterator)
    const groupedWarnings = getGroupedFails('warnings', iterator)
    return (
      <div>
        <h3>{props.iteratorName}</h3>
        <h4>Discards:</h4>
        {groupedDiscards.map(({ reason, values }) => (
          <div style="margin-left: 2em">
            <b>{reason}</b>: {values.map(x => <div style="margin-left: 2em">{x}</div>)}
          </div>
        ))}
        <h4>Warnings:</h4>
        {groupedWarnings.map(({ reason, values }) => (
          <div style="margin-left: 2em">
            <b>{reason}</b>: {values.map(x => <div style="margin-left: 2em">{x}</div>)}
          </div>
        ))}
        <h4>Advanced data:</h4>
        <blockquote>
          <Inspector expandLevel={3} data={iterator} />
        </blockquote>
      </div>
    )
  }
}



