import stringifyObject from 'stringify-object'
import * as R from 'ramda'

const formatProfile = profile => {
  return stringifyObject(R.omit(['category', 'geolocation'], profile), {
    indent: '  ',
    inlineCharacterLimit: 12
  })
}

export default ({ fixture: { profile, fixtureId } }) => (
  <div class="FixtureHeader">
    <span class="FixtureHeader__location">{profile.geolocation.city}</span>
    <span class="FixtureHeader__name">{fixtureId.slice(1, -1)}</span>
    <span class="FixtureHeader__profile">{formatProfile(profile)}</span>
  </div>
)



