import { Component } from 'preact'
import FixtureResult from './FixtureResult'
import ReportList from './ReportList'
import Summary from './Summary'
import { inflate } from 'pako'
import ReactSpinner from 'react-spinner'
import 'react-spinner/react-spinner.css'

const linksRe = /href="([^"]*\.json\.gz)"/g
const linkRe = /href="([^"]*\.json\.gz)"/

const getReports = storageAddress => {
  return fetch(storageAddress)
    .then(x => x.text())
    .then(html => {
      return html
        .match(linksRe)
        .map(x => x.match(linkRe)[1])
        .sort()
        .reverse()
    })
}

const getReport = url => {
  return fetch(url, { credentials: 'same-origin' })
    .then(x => x.arrayBuffer())
    .then(buffer => {
      const inflated = inflate(buffer, { to: 'string' })
      return JSON.parse(inflated)
    })
}

export default class App extends Component {
  state = {
    report: [],
    reports: [],
    showGood: true,
    showExtra: true
  }
  componentDidMount() {
    this.openLatest()
  }
  refresh = () => {
    if (window.location.host === 'build.findy.com') {
      return new Promise(resolve => {
        const reports = ['reports/report.json.gz']
        this.setState({ reports })
        resolve(reports)
      })
    }
    return getReports('reports/')
      .then(reports => {
        this.setState({ reports })
        return reports
      })
      .catch(e => {
        console.error(e)
        this.setState({ reports: [] })
      })
  }
  setReport = (report) => {
    // for debug
    window.report = report
    this.setState({ report })
  }
  onReportSelect = name => {
    this.setState({ loading: true })
    getReport(`${name}`)
      .then(this.setReport)
      .finally(() => this.setState({ loading: false }))
  }
  openLatest = () => {
    this.refresh().then(reports => {
      if (reports[0]) {
        this.onReportSelect(reports[0])
      }
    })
  }
  changeShowGood = e => {
    this.setState({ showGood: e.target.checked })
  }
  changeShowExtra = e => {
    this.setState({ showExtra: e.target.checked })
  }
  render() {
    return (
      <div>
        <div class="Settings">
          <button onClick={this.refresh}>Update List</button>{' '}
          <button onClick={this.openLatest}>Open Latest</button>{' '}
          <ReportList
            reports={this.state.reports}
            onReportSelect={this.onReportSelect}
          />
        </div>
        <div class="Settings">
          <label>
            Show Good:{' '}
            <input
              type="checkbox"
              checked={this.state.showGood}
              onChange={this.changeShowGood}
            />
          </label>
          <label>
            Show Details:{' '}
            <input
              type="checkbox"
              checked={this.state.showExtra}
              onChange={this.changeShowExtra}
            />
          </label>
        </div>
        {this.state.loading && <ReactSpinner />}
        {this.state.report.length > 0 &&
          <div class="Results">
            <Summary report={this.state.report}/>
            {this.state.report.map(data => (
              <FixtureResult
                showGood={this.state.showGood}
                showExtra={this.state.showExtra}
                fixture={data}
              />
            ))}
          </div>
        }
      </div>
    )
  }
}



