import { parse, format } from 'date-fns'

const timezoneRe = /\d{8}T\d{6}Z/

const humanizeReportName = name => {
  const reportName = name.slice(name.lastIndexOf('/') + 1, name.indexOf('.'))
  return timezoneRe.test(reportName) ? format(parse(reportName), 'YYYY-MM-DD HH:mm:ss Z') : reportName
}

export default ({ reports, onReportSelect }) => {
  return (
    <select onChange={e => onReportSelect(e.target.value)}>
      <option>Select report ({reports.length})</option>
      {reports.map(report => (
        <option value={report} key={report}>
          {humanizeReportName(report)}
        </option>
      ))}
    </select>
  )
}


