import { Component } from 'preact'
import * as R from 'ramda'
import FixtureHeader from './FixtureHeader'
import IteratorResult from './IteratorResult'
import StepsDetails from './StepsDetails'

export default class FixtureResult extends Component {
  toggleIterator(activeIterator) {
    this.setState(state => {
      if (state.activeIterator === activeIterator) {
        return { activeIterator: undefined }
      } else {
        return { activeIterator }
      }
    })
  }

  render(props, state) {
    const { fixture } = props
    return (
      <div class="FixtureResult">
        <FixtureHeader fixture={fixture} />
        <div class="FixtureResult__iterators">
          {fixture.iterators.map(iterator => (
            <IteratorResult
              iterator={iterator}
              showGood={props.showGood}
              showExtra={props.showExtra || state.activeIterator === iterator}
              onClick={() => this.toggleIterator(iterator)}
            />
          ))}
        </div>
        {state.activeIterator && (
          <StepsDetails
            iterator={state.activeIterator}
          />
        )}
      </div>
    )
  }
}



